import * as React from 'react';
import CollapsableListItem from './CollapsableListItem';
import { withStyles } from '@material-ui/core/styles';
import { getFiles } from '../../Service/debugService';

const styles = theme => ({
  dirTree: {
  }
});

export interface TreeItem {
  index: number;
  name: string;
  title: string;
  depth: number;
  loaded: boolean;
  isDir: boolean;
  children?: number[];
  parentIndex?: number;
  disabled?: boolean;
}

interface Props {
  classes: any;
}

interface State {
  treeItems: TreeItem[];
}

class DirTree extends React.Component<Props, State> {

  constructor(props) {
    super(props);
    this.state = {
      treeItems: [{
        index: 0,
        name: '',
        title: "<root>",
        loaded: false,
        isDir: true,
        depth: 0
      }]
    }
    this.handleLoad = this.handleLoad.bind(this);
  }

  async handleLoad(itemIndex: number): Promise<boolean> {
    const item = this.state.treeItems[itemIndex];
    let itemCount = this.state.treeItems.length;
    if(!item.loaded) {
      try {
        const dirDesc: any = await getFiles(item.name);
        const newItems = [
          ...this.state.treeItems,
          ...dirDesc.files.map(val => {
            item.children = [...item.children, itemCount];
            return ({
              index: itemCount++,
              name: val.name,
              title: val.name,
              loaded: !val.isDir,
              isDir: val.isDir,
              depth: item.depth + 1,
              parentIndex: item.index
            });
          })
        ];
        item.loaded = true;
        this.setState({
          treeItems: newItems
        });
        return true;
      }
      catch (err) {
        return false;
      }

    }
    return true;
  }

  render() {
    return (<div className={this.props.classes.dirTree}>
      <CollapsableListItem
        items={this.state.treeItems}
        rootItem={this.state.treeItems[0]}
        children={this.state.treeItems[0].children}
        onLoad={this.handleLoad}/>
    </div>);
  }
}

export default withStyles(styles)(DirTree);
