import * as React from 'react';

import Button from '@material-ui/core/Button';

import { withStyles } from '@material-ui/core/styles';

interface Props {
  classes: any;
}

interface State {
  waitingResult: boolean;
}

const styles = theme => ({
  button: {
    margin: theme.spacing.unit
  }
});

class DebugButtons extends React.Component<Props, State> {
  constructor(props) {
    super(props);
    this.state = {
      waitingResult: false
    };
  }

  render() {
    return <div>
      <Button variant="contained" color="primary" className={this.props.classes.button}>Debug</Button>
      <Button variant="contained" className={this.props.classes.button}>Build All</Button>
      <Button variant="contained" color="secondary" className={this.props.classes.button}>Stop</Button>
    </div>;
  }
}

export default withStyles(styles)(DebugButtons);
