import * as React from 'react';

import Collapse from '@material-ui/core/Collapse';
import { TreeItem } from './DirTree';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';

interface Props {
  items: TreeItem[];
  rootItem: TreeItem;
  children: number[];
  onLoad: (itemIndex: number) => Promise<boolean>;
}

interface State {
  open: boolean;
  error: boolean;
  loading: boolean;
}

class CollapsableListItem extends React.Component<Props, State> {

  constructor(props) {
    super(props);
    this.state = {
      open: false,
      error: false,
      loading: false
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    if(!this.props.children) {
      this.props.onLoad(this.props.rootItem.index)
        .then(res => {
          if(res) {
            this.setState({
              open: true,
              error: false,
              loading: false
            });
          }
        })
        .catch(err => {
          console.error(err);
          this.setState({
            open: false,
            error: true,
            loading: false
          });
        });
    }
    this.setState({
      open: true
    });
  }

  render() {
    console.log(this.props.children);
    return ([
        <ListItem button onClick={this.handleClick}>
          <ListItemText inset primary={this.props.rootItem.title} />
          {this.state.open ? <ExpandLess color="action" /> : <ExpandMore color="action" />}
        </ListItem>,
        <Collapse in={this.state.open}>
          <List component="div">
            {this.props.children ?
              this.props.children
              .map(cIndex => this.props.items[cIndex])
              .map(val => {
                if(val.isDir)
                  return (<CollapsableListItem
                    key={val.index}
                    items={this.props.items}
                    children={val.children}
                    rootItem={val}
                    onLoad={this.props.onLoad}
                  />);
                else
                  return (<ListItem button key={val.index}>
                    <ListItemText inset primary={val.title} />
                  </ListItem>);
                }) : <ListItem>
              <ListItemText inset primary="Loading..." />
            </ListItem>}
          </List>
        </Collapse>]);
  }
}

export default CollapsableListItem;
