import * as React from 'react';

import { withStyles } from '@material-ui/core/styles';

import { Controlled as CodeMirror } from 'react-codemirror2';

import 'codemirror/lib/codemirror.css';
import 'codemirror/theme/material.css';

interface BreakpointDefinition {
  filename: string;
  line: number;
}

interface Props {
  value: string;
  execLine?: number;

  classes: any;
}

interface State {
  breakpoints: BreakpointDefinition[];
}

const styles = theme => ({
  codeDisplay: {
    paddingTop: 10,
    paddingLeft: 10
  }
});

class CodeDisplay extends React.Component<Props, State> {

  constructor(props: Props) {
    super(props);
    this.state = {
      breakpoints: []
    };
  }

  render() {
    return (<CodeMirror
      value={this.props.value}
      onBeforeChange={() => {
        console.log("On before change");
      }}
      onGutterClick={(editor, lineNumber, gutter, event) => {
        console.log("Is that what I'm looking for?");
        console.log(lineNumber);
      }}
      className={this.props.classes.codeDisplay}
      options={{
        lineNumbers: true,
        readOnly: true,
        theme: 'material',
        gutters: [
          "gutter-normal",
          "gutter-breakpoint",
          "gutter-exec"
        ]
      }}
    />);
  }
}



export default withStyles(styles)(CodeDisplay);
